@import url(https://fonts.googleapis.com/css2?family=DM+Mono:ital,wght@1,300&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body.dark-mode{background-color:#000000;color:#ffffff}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.app{display:flex;flex-direction:column;font-family:'DM Mono', monospace}.x{display:flex;align-items:flex-end;flex-direction:column}

.header{display:flex;flex-direction:column;align-items:center}.header h1{font-size:2em;margin:0}.header h2,.header h2>a{color:#585858;margin:0}.header .links{display:flex;width:100%;text-decoration:none;color:black;border-bottom:1px solid black;font-size:2em;margin-top:20px}.header .links a{color:black;border-top:1px solid black;width:inherit;text-align:center}.header .links a:link,.header .links a:visited{text-decoration:none}.header .links a:hover,.header .links a:active{text-decoration:underline}.header-dark{display:flex;flex-direction:column;align-items:center}.header-dark h1{font-size:2em;margin:0}.header-dark h2,.header-dark h2>a{color:#ffffff;margin:0}.header-dark .links{display:flex;width:100%;text-decoration:none;color:#ffffff;border-bottom:1px solid #ffffff;font-size:2em;margin-top:20px}.header-dark .links a{color:#ffffff;border-top:1px solid #ffffff;width:inherit;text-align:center}.header-dark .links a:link,.header-dark .links a:visited{text-decoration:none}.header-dark .links a:hover,.header-dark .links a:active{text-decoration:underline}@media only screen and (min-device-width: 375px) and (max-device-width: 667px) and (-webkit-min-device-pixel-ratio: 2){.header h1,.header-dark h1{font-size:1.5em}.header h2,.header-dark h2{font-size:1em}.header h1,.header h2,.header-dark h1,.header-dark h2{text-align:center}}

img{border-radius:100px;border:1px solid white;margin:10px;width:200px;height:200px}@media only screen and (min-device-width: 375px) and (max-device-width: 667px) and (-webkit-min-device-pixel-ratio: 2){img{width:150px;height:150px}}

.main{text-align:center}

.dark-mode-toggle,.dark-mode-toggle-dark{margin:10px}.dark-mode-toggle svg,.dark-mode-toggle-dark svg{border:1px solid;font-size:30px;padding:5px;border-radius:100px}.dark-mode-toggle svg{color:#000000;border-color:#000000}.dark-mode-toggle-dark svg{border-color:#ffffff;color:#ffffff}

